import React, { ChangeEvent, KeyboardEvent, useState } from 'react'
import styles from './Input.module.scss'

export const Input: React.FC<InputPropsType> = ({
    sendCallback,
    placeholder = 'Find your job',
}) => {
    const [inputValue, setInputValue] = useState<string>('')

    const onChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        setInputValue(event.currentTarget.value)
    }

    const sendValueHandler = () => {
        sendCallback(inputValue)
        setInputValue('')
    }

    const KeyBoardsValueHandler = (event: KeyboardEvent<HTMLInputElement>) => {
        if (event.code === 'Escape') {
            setInputValue('')
        }
        if (event.code === 'Enter') {
            sendValueHandler()
        }
    }

    return (
        <div className={styles['wrapper']}>
            <input
                placeholder={placeholder}
                className={styles['input']}
                onChange={onChangeHandler}
                onKeyUp={KeyBoardsValueHandler}
                value={inputValue}
            />
            <div className={styles['input-button']} onClick={sendValueHandler}>
                <svg
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <g opacity="0.64">
                        <path
                            fillRule="evenodd"
                            clipRule="evenodd"
                            d="M14.9056 16.3199C13.551 17.3729 11.8487 18 10 18C5.58172 18 2 14.4183 2 10C2 5.58172 5.58172 2 10 2C14.4183 2 18 5.58172 18 10C18 11.8487 17.3729 13.551 16.3199 14.9056L21.7071 20.2929C22.0976 20.6834 22.0976 21.3166 21.7071 21.7071C21.3166 22.0976 20.6834 22.0976 20.2929 21.7071L14.9056 16.3199ZM16 10C16 13.3137 13.3137 16 10 16C6.68629 16 4 13.3137 4 10C4 6.68629 6.68629 4 10 4C13.3137 4 16 6.68629 16 10Z"
                            fill="white"
                        />
                    </g>
                </svg>
            </div>
        </div>
    )
}

interface InputPropsType {
    sendCallback: (value: string) => void
    placeholder?: string
}
